'use strict';

window.su = window.su || {};
su.ratingControlEnabled = false;
su.feedbackFormSubmitted = false;

su.productFilter = function() {
  $("#form-productFilter").on("submit", function(e) {
    e.preventDefault();
    var u = new Url;
    var form = $(this);
    var categories= "";

    //get the values from all checked inputs and then build a query string to filter the products
    $(form).find('input:checkbox:checked').each(function() {
      categories = categories.concat( $(this).val() + "," ) ;
    });

    u.query.filter = categories.replace(/,\s*$/, "");
    window.location.href = u;
  });
}

su.productSort = function() {
  $("#form-productSort").find('input[type="radio"]').on("click", function(event) {
    var u = new Url;
    var el = $(this);

    u.query.sort = el.val();
    window.location.href = u;
  });
}

su.signOn = function() {
  $(".form-signon").submit();
}

su.productSlider = function() {
  $('.bxslider').bxSlider({
    mode: 'horizontal',
    infiniteLoop: true,
    oneToOneTouch: false,
    auto: true,
    pause: 4000,
    onSliderLoad: function(){
      $("#slider").addClass("fadeIn");
    }
  });
}

su.activateMenu = function() {
  $("#menu").mmenu({
     "offCanvas": {
        "position": "right"
     },
     "extensions": [
        "fullscreen"
     ],
     "navbar": {
       "add": false
     },
     "navbars": [{
 			   "height": 3,
 			     "content": [
     				'<a href="/" class="logo"><img src="/images/logo-serveup.svg" /></a>',
     				'close',
            'title'
 			    ]
 		  }],
  });
}

su.activateSliders = function() {
  $(".slideToggle").on("click", function(e) {
    e.preventDefault();
    $button = $(this);

    $(".slideContainer").slideToggle(300, function() {
      $button.toggleClass("hasSlided");

      if( ! su.ratingControlEnabled && ! su.feedbackFormSubmitted ) {
        su.ratingControlEnabled = true;
        window.ratingControl = new RatingControl(document.querySelector(".rating-control"));
      } else {
        su.ratingControlEnabled = false;
        delete window.ratingControl;
      }
    });
  })
}

su.activateFeedback = function() {
  $(".s-feedBack").find(".slideContainer").hide();
  var $form = $("#form-feedBack");

  $("#form-feedBack--submit").on("click", function(e) {
    e.preventDefault();

    if( $form.find("input[name='fields[recommend]']").is(":checked") ) {
      var formHeight = $("#form-feedBack").css("height");
      var formData = $form.serialize();

      $(".s-feedBack .success-container").addClass("loading animate");
      $form.css("height",formHeight).empty().animate({ height: 200 }, { duration: 300 });

      $.post( '/', formData, function(data) {
          $(".s-feedBack .success-container").removeClass("loading").addClass("loaded");
          su.feedbackFormSubmitted = true;
       });

    } else {
      $(".recommend-container").find("label").addClass("error");
      $(".recommend-container").append('<p class="error">Please select one of the above.</p>');
    }
  });
}

su.activateMobileOptInForm = function() {

  $("#form-register").validate({

    errorPlacement: function(error, element) {
      if (element.attr("name") == "terms" ) {
        error.appendTo( $(element).parent("p") );
      } else {
        error.insertAfter(element);
      }
    },

    submitHandler: function(form) {
      var formHeight = $(form).css("height");
      var formData = $(form).serialize();

      $(".s-mobileOptIn .success-container").addClass("loading animate");
      $(form).css("height",formHeight).empty().animate({ height: 200 }, { duration: 300 });

      $.post( '/', formData, function(data) {
          $(".s-mobileOptIn .success-container").removeClass("loading").addClass("loaded");
       });
    }
  });
}

su.activateColesButtons = function() {

  $(".coles-controls").find(".btn-up").on("click", function(e) {
    e.preventDefault();
    $('html, body').animate({scrollTop: '-=250px'}, 250);
  });

  $(".coles-controls").find(".btn-down").on("click", function(e) {
    e.preventDefault();
    $('html, body').animate({scrollTop: '+=250px'}, 250);
  });
}

su.activateQuizes = function() {

  //setup form submit event
  $(".quiz-container").on("submit", ".form-quiz", function(e) {
    e.preventDefault();
    var $form = $(this);
    var $container = $form.parents(".quiz-container");
    var containerHeight = $(".quiz-container").css("height");

    var posting = $.post( $form.attr("action"), $form.serialize() );
    $container.removeClass("fadeIn").addClass("fadeOut animating").css("height",containerHeight);

    posting.done( function(data) {
      $container.empty().removeClass("fadeOut").addClass("fadeIn").css("height",'');
      setTimeout(function() {
          $container.removeClass("animating");
      }, 300);
      $container.append(data);
    });
  });

  //setup load quiz event
  $(".quiz-container").on("click", '.btn-loadQuiz', function(e) {
    e.preventDefault();
    var $container = $(this).parents(".quiz-container");
    var containerHeight = $container.css("height");

    //lets also update the pagination numbers
    if( $container.find(".quiz-response").hasClass("quiz-pagination") ) {
      if( $container.find(".quiz-response").attr("data-quizsuccess") == 1 ) {
        var paginationCount = $container.find(".quiz-response").attr("data-paginatenext");
        $(".pagination-current").text(paginationCount);
      }
    }

    var posting = $.get( $(this).attr("href") );
    $container.removeClass("fadeIn").addClass("fadeOut animating").css("height",containerHeight);

    posting.done( function(data) {
      $container.empty().removeClass("fadeOut").addClass("fadeIn").css("height",'');
      setTimeout(function() {
          $container.removeClass("animating");
      }, 300);
      $container.append(data);
    });
  });

  //setup quiz button events
  $(".quiz-container").on("click", 'input[type="radio"]', function() {
    var $container = $(this).parents(".quiz-container");

    if( $(this).is(":checked") ) {
      $container.find('input[name="question_id"]').val( $(this).attr("data-id") );
    }
  });
}

su.accordion = function() {
  var activeTab = 0;
  $(".accordion-header").each(function(i){
    if(  $(this).hasClass("active") ) {
      activeTab = i;
      return false;
    }
  }).promise().done(function () {

    $( "#accordion" ).accordion({
      heightStyle: "content",
      active: activeTab,
      collapsible: true
    });
  });

}

su.init = function() {
  su.productSort();
  su.productFilter();
  su.signOn();
  su.productSlider();
  su.activateMenu();
  su.activateSliders();
  su.activateFeedback();
  su.activateQuizes();
  su.activateColesButtons();
  su.activateMobileOptInForm();
  su.accordion();
}

//main body function go here
$(function(){
  su.init();
});
